<template>
	<div id="bargain">
		<div class="top">
			<img src="/dist/image/bargain/backgroundtop@2x.png" />
			<img src="/dist/image/bargain/procedure@2x.png" />
			<div class="time">
				<a>{{day < 10 ? "0" + day : day}}</a>
				<span></span>
				<a>{{hour < 10 ? "0" + hour : hour}}</a>
				<span></span>
				<a>{{minute < 10 ? "0" + minute : minute}}</a>
				<span></span>
				<a>{{second < 10 ? "0" + second : second}}</a>
				<span></span>
			</div>
		</div>
		<ul class="list">
			<li v-for="item in list" >
				<img v-lazy="item.proImg" class="img" />
				<div class="detail-wrap">
					<p>{{item.proName}}</p>
					<p>{{item.taxDetails}}</p>
					<p><span>{{item.price}}</span></p>
					<p v-if="item.status == 3" @click="goHelp(item.id)"><span></span></p>
					<p v-else-if="item.status == 1" @click="goHelp(item.id)"><span>...</span></p>
					<p v-else><span></span></p>
				</div>
			</li>
		</ul>
		<div class="rule">
			<h3></h3>
			<p>1.</p>
			<p>2.)</p>
			<p>3.</p>
			<p>4.0.8-1.50.2-0.5</p>
			<p>5.</p>
			<p>6.</p>
			<p>7.</p>
		</div>
		<v-alert v-if="alertshow" :context="context"></v-alert>
	</div>
</template>

<script>
	import config from "../config/config.js";
	import Alert from "../alert.vue";
	import share from "../share/share.js";
	export default{
		components:{
			"v-alert": Alert
		},
		data(){
			return {
				alertshow:false,
				context:"",
				height:0,
				list:[],
				end:false,
				day:0,
				hour:0,
				minute:0,
				second:0,
				timer:null
			}
		},
		methods:{
			showActivity(){
				this.show = true;
			},
			hide(){
				this.show = false;
			},
			
			goHelp(id){
				if(this.end){
					this.alertshow = true;
					this.context = "";
					setTimeout(() => {
						this.alertshow = false;
					},1000)
					return;
				}
				axios.post("/activity/PushCut",{
					catid:id
				},config).then(res => {
					if(res.data.code == 0){
						sessionStorage.setItem("shareid",res.data.data.shareid);
						this.$router.push("/help");
					}else{
						this.alertshow = true;
						this.context = "";
						setTimeout(() => {
							this.alertshow = false;
						},1000)
					}
				})
			}
		},
		activated(){
			share({
				title:"0",
				desc:'0',
				imgUrl:"http://ws.tianmaoetong.com/dist/image/bargain/backgroundtop@2x.png",
				link:"/bargain"
			});
			
			config.headers.userid = sessionStorage.getItem("userid");
			config.headers.usertoken = sessionStorage.getItem("usertoken");
			this.height = document.body.offsetHeight;
			
			axios.post("/activity/CutMoney",{},config).then(res => {
				if(res.data.code == 0){
					this.list = res.data.data.proList;
					var time = res.data.data.other.eTime;
					clearInterval(this.timer);
					this.timer = setInterval(() => {
						if(time == 0){
							this.end = true;
							this.alertshow = true;
							this.context = "";
							setTimeout(() => {
								this.alertshow = false;
							},2000);
						}
						this.day = Math.floor(time/(3600*24));
						this.hour = Math.floor(time%(3600*24)/3600);
						this.minute = Math.floor(time%3600/60);
						this.second = Math.floor(time%60);
						time--;
					},1000)
				}else if(res.data.code == 432){
					this.list = res.data.data.proList;
					this.end = true;
					this.alertshow = true;
					this.context = "";
					setTimeout(() => {
						this.alertshow = false;
					},2000);
				}
			})
		}
		
	}
</script>

<style>
</style>


