<template>
	<div style="font-size: .3rem;">
		<p v-if="type == 1">
			
		</p>
		<p v-if="type == 2">
			 <a @click="other()" style="color: #00f;"></a>
		</p>
		<p v-if="type == 3">
			
		</p>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				type:1
			}
		},
		activated(){
			this.type = this.$route.params.type;
		},
		methods:{
			other(){
				this.$router.push("/bargain");
			}
		}
	}
</script>

<style>
</style>


